.send {
	width: 92%;
	display: flex;
	justify-content: flex-end;
	padding: 10px 6px 0 0;
    &-button {
        padding: 16px 37.5px;
        border: none;
    }
}

@media (max-width: 767px) {
    .send {
        justify-content: center;
        width: 100%;
        padding: 28px 0 0 0;
        &-button {
            width: 100%;
        }
    }
}